import { Link, Typography } from "@mui/material";
import React from "react";

function SuccessForm() {
  let AppointmentId = localStorage.getItem("AppointmentId");

  let Success;
  if (AppointmentId) {
    Success = (
      <React.Fragment>
        <Typography variant="h5" gutterBottom>
          Thank you for your Appointment
        </Typography>
        <Typography variant="subtitle1">
          {`Your order number is #${AppointmentId}. We have emailed your Appointment
          confirmation, and Visit Us Again.`}
        </Typography>
        <Typography textAlign="center">
          <Link href="/appointments">Go to My Appointment</Link>
        </Typography>
        <Typography textAlign="center">
          <Link href="/">Go to HomePage</Link>
        </Typography>
      </React.Fragment>
    );
  } else {
    Success = (
      <React.Fragment>
        <Typography variant="h5" gutterBottom>
          Sorry Your Appointment Couldn't Placed .
        </Typography>
      </React.Fragment>
    );
  }
  return <React.Fragment>{Success}</React.Fragment>;
}

export default SuccessForm;
